.p-overview {

}

.p-overview__inner {
  @include mq('tab') {
    margin: 0 auto;
    width: 100%;
  }
}

.p-overview__contents {
  margin-top: rem(47);

  @include mq('tab') {
    background: linear-gradient( -90deg, transparent 0%, transparent 30%, #333 30%, #333 100%);
    width: 100%;
    margin: rem(40) auto 0;
    padding: rem(80) 7.03% rem(81);
  }
}

.p-overview__body {
  width: 100%;
  margin-left: auto;
  margin-right: auto;

  @include mq('tab') {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    max-width: $l-inner;
  }
}

.p-overview__title {
  letter-spacing: rem(2);
  position: relative;

  @include mq('tab') {
    letter-spacing: rem(0.5);
    max-width: rem(1280);
    margin: 0 auto;
    width: 100%;
  }
}

.p-overview__title span {
  @include mq('tab') {
    left: 7%;
    top: rem(-52);
    transform: translateX(0);
  }
}

.p-overview__picture {
  width: 100%;
  position: relative;

  @include mq('tab') {
    margin-left: 2.9%;
    width: 50.63%;
  }
}

.p-overview__picture::before {
  content:"";
  display: block;
  padding-top: 69.6%;
}

.p-overview__picture img {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.p-overview__content {
  padding: rem(24) rem(19) rem(21);
  background-color: #333;

  @include mq('tab') {
    background-color: transparent;
    padding: 0;
    width: 46.36%;
  }
}

.p-overview__main-title {
  font-size: rem(20);
  letter-spacing: rem(2);
  margin-left: rem(19);
  text-align: center;

  @include mq('tab') {
    font-size: rem(25);
    letter-spacing: rem(0.5);
    text-align: left;
    margin-left: rem(-2);
    margin-top: rem(-7);
  }
}

.p-overview__text {
  font-size: rem(16);
  letter-spacing: rem(2);
  margin-top: rem(23);
  line-height: rem(23.17);

  @include mq('tab') {
    letter-spacing: rem(0.5);
    line-height: 2.1875;
    margin-top: rem(36);
    margin-left: rem(-2);
  }
}

.p-overview__button-more {
  display: flex;
  justify-content: center;
  text-align: center;
  margin-top: rem(26);
  
  @include mq('tab') {
    margin-top: rem(48);
    justify-content: start;
    margin-left: rem(-2);
  }
}

.p-overview__button-swipe {
  background-color: #333;
  font-size: rem(20);
  color: #fff;
  border: 1px solid #fff;
  transition: .3s;
  font-size: rem(20);
  overflow: hidden;
  position: relative;
  z-index: 1;

  @include mq('tab') {
    font-size: rem(16);
  }
}

.p-overview__button-swipe::before {
  content:"";
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transform-origin: right top;
  transform: scale(0, 1);
  transition: transform .3s;
}

.p-overview__button-swipe:hover {
  color: #333;
}

.p-overview__button-swipe:hover:before {
  transform-origin: left top;
  transform: scale(1, 1);
}