.l-diagonal-line__downward,
.l-diagonal-line__upward {
  position: relative;
  overflow: hidden;
}

.l-diagonal-line__downward::after {
  position: absolute;
  content: "";
  top: 4.375rem;
  left: 50%;
  width: 0.0625rem;
  height: calc( 776 / 375 * 100vw);
  background: #F9F871;
  transform: rotate(-29deg);
  z-index: -1;
}

@media screen and (min-width: 768px) {
  .l-diagonal-line__downward::after {
    top: -10.625rem;
    left: 47%;
    height: calc( 1714 / 1280 * 100vw);
    transform: rotate(-45deg);
  }
}

.l-diagonal-line__upward::after {
  position: absolute;
  content: "";
  top: -34.375rem;
  left: 50%;
  width: 0.0625rem;
  height: calc( 1448 / 375 * 100vw);
  background: #F9F871;
  transform: rotate(15deg);
  z-index: -1;
}

@media screen and (min-width: 768px) {
  .l-diagonal-line__upward::after {
    top: -22.9375rem;
    left: 45%;
    height: calc( 2000 / 1280 * 100vw);
    transform: rotate(45deg);
  }
}

